<template>
  <div class="grade full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="grade-wrapper">
          <div class="grade-title icon-class"></div>
          <div class="grade-desc">认证投资人等级分为:青铜、白银、黄金、钻石、荣耀，根据不同荣耀值划分。</div>
          <div class="grade-image-wrapper">
            <div class="grade-image-class"></div>
          </div>
          <div class="grade-title icon-privileged"></div>
          <div class="grade-desc">按投资人登记对应不同特权，等级越高权限越多。</div>
          <div class="grade-image-wrapper" ref="imageScroll">
            <div class="grade-image-privileged"></div>
          </div>
        </div>
      </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import BScroll from 'better-scroll'
export default {
  components: {
    PemScrollView
  },
  mounted () {
    setTimeout(() => {
      this._initPage()
    }, 20)
  },
  methods: {
    _initPage () {
      this.imageScroll = new BScroll(this.$refs.imageScroll, {
        scrollX: true,
        scrollY: false,
        bounce: false
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .grade
    font-size: 0.24rem
    .page-panel
      background-color: $color-panel
      
      .grade-wrapper
        padding: 0.3rem 0 0.98rem 0
        .grade-title
          width: 3.14rem
          height: 0.68rem
          margin: 0 auto
          background-size: 3.14rem 0.68rem
          &.icon-class
            bg-image('icon-Class description')
          &.icon-privileged
            bg-image('icon-Privileged description')
        .grade-desc
          font-size: 0.36rem
          color: $color-text-body
          letter-spacing: -0.0082rem
          line-height: 0.48rem
          text-indent: 0.72rem
          padding: 0.3rem
        .grade-image-wrapper
          width: 6.9rem
          padding-bottom: 0.3rem
          .grade-image-class
          .grade-image-privileged
            margin: 0 0.3rem
            background-repeat: no-repeat
          .grade-image-class
            width: 6.92rem
            height: 6.14rem
            background-size: 6.92rem 6.14rem
            bg-image('image-Class description')
          .grade-image-privileged
            width: 10.34rem
            height: 6.36rem
            background-size: 10.34rem 6.36rem
            bg-image('image-Privileged description')
</style>



